<template lang="html">
  <div class="address-page__item detail clearfix" :style="{padding: padding}">
    <div class="address-page__left fl">详细地址:</div>
    <div class="address-page__right">
      <textarea class="address-page__detail" v-model="currValue"  @input="$emit('input', currValue)" @change="$emit('change', currValue)"
      name="address-page__detail" width="500px" placeholder="请输入地址" resize="none"></textarea>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    padding: {
      type: String,
      default: '0'
    },
    value: {}
  },
  data () {
    return {
      currValue: this.value
    }
  },
  watch: {
    value (val) {
      this.currValue = val
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.address-page__item {
  width: 100%;
  font-size: .32rem;
  .address-page__left {
    width: 1.46rem;
    padding-top: .48rem;
    // line-height: 1.28rem;

  }
  &.detail {
    border-bottom: none;
    height: 1.76rem;
    .address-page__left {
      padding-top: .3rem;
    }
  }
  .address-page__detail {
    width: 100%;
    height: 1.76rem;
    color: @lightColor;
    border: none;
    font-size: .28rem;
    padding: .3rem .22rem .3rem .12rem;
    &::placeholder {
      color: @lightColor;
    }
  }
  .address-page__right {
    margin-left: 1.46rem;
    height: 100%;
  }
}
</style>



