<template lang="html">
  <div class="content-title" :style="{marginTop: marginT, padding: padding}">
    <div class="content-title-text clearfix" :style="{borderBottom: borderB}">
      <div class="content-title-text__left fl">{{textLeft}}</div>
      <slot></slot>
      <div class="content-title-text__right fr" :class="{'wrongColor': warn}">
        <span :style="{color: rightColor}">{{textRight}}</span>
        <img :src="image || defaultimg" class="avatar" v-if="isAvatar" />
        <img class="arrow" src="../../assets/img/arrow@2x.png"  v-if="isShow" />
      </div>
    </div>
  </div>
</template>
<!-- <img src="../../assets/img/phone@2x.png"  v-if="true" /> -->
<script>
import defaultimg from '@/assets/img/personcenter/ph2@3x.png'
export default {
  props: {
    image: String,
    textRight: {
      type: String,
      default: '右侧文字'
    },
    textLeft: {
      type: String,
      default: '左侧文字'
    },
    isShow: {
      type: Boolean,
      default: false
    },
    isAvatar: {
      type: Boolean,
      default: false
    },
    marginT: {
      type: String,
      default: '0rem'
    },
    padding: {
      type: String,
      default: '0 .22rem'
    },
    borderB: {
      type: String,
      default: '0'
    },
    rightColor: {
      type: String,
      default: '#666'
    },
    warn: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      defaultimg
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.content-title {
  position: relative;
  background: @whiteColor;
}
.content-title-text {
  font-size: .32rem;
  width: 100%;
  height: .88rem;
  text-align: center;
  line-height: .88rem;
  overflow: hidden;
  color: @defaultColor;
  background-color: @whiteColor;
  .content-title-text__left {
    font-size: .32rem;
  }
  .content-title-text__right {
    font-size: .3rem;
    .avatar {
      width: .65rem;
      height: .65rem;
      border-radius: 50%;
      vertical-align: middle;
    }
    .arrow {
      width: .44rem;
      height: .44rem;
      position: relative;
      top: .13rem;
      left: .15rem;
    }
  }
}
</style>



