<template lang="html">
  <!-- 提交按钮 -->
  <div class="sub-button" :style="{'margin-top': marginTop || margintop}">
    <input type="button" name="" :value="text" :style="{color: color, background: bgcolor, borderRadius: borderRadius}">
  </div>
</template>

<script>
export default {
  props: {
    bgcolor: {
      type: String,
      default: '#2cc482'
    },
    color: {
      type: String,
      default: '#fff'
    },
    marginTop: String,
    margintop: {
      type: String,
      default: '0rem'
    },
    borderRadius: {
      type: String,
      default: '0'
    },
    text: {
      type: String,
      default: '提交'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.sub-button {
  width: 100%;
  height: .88rem;
  padding: 0 .22rem;
  input {
    width: 100%;
    height: .88rem;
    font-size: .34rem;
    border: none;

  }
}
</style>



