<template>
  <div class="card-add-address">
    <!-- 头部 -->
    <default-head header="添加地址" path="/cardaddress"></default-head>
    <div class="address-page">
      <!-- 表单 -->
      <form action="#">
        <div class="address-page__list">
          <count-item @click.native="$router.push({path: 'searchpoint', query: { sign: $route.query.sign}})" borderB="1px solid #ddd" :isShow="true" textLeft="所在地区" textRight="请选择" rightColor="#999"></count-item>
          <address-input v-model="address"  padding="0 .22rem"></address-input>
        </div>
        <!-- 提交按钮 -->
        <sub-button @click.native="submitAddress"></sub-button>
      </form>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import subButton from '@/components/common/subButton'
import countItem from '@/components/personcenter/countItem'
import addressInput from '@/components/common/addressInput'

export default {
  components: { defaultHead, subButton, countItem, addressInput },
  data () {
    return {
      position: {},
      address: '',
      sign: ''
    }
  },
  created () {
    this.sign = this.$route.query.sign
    this.initPage()
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      let address = sessionStorage.getItem('searchPosition')
      if (address) {
        this.position = JSON.parse(address)
        this.address = this.position.address + this.position.title
      }
    },
    submitAddress () {
      // 判断详细地址是否为空
      if (!this.isLogin()) return
      if (!this.address && this.position) {
        minutils.Toast({ message: '详细地址不能为空', position: 'top' })
        return
      }

      var data = {
        userId: this.userId,
        type: this.sign === 'company' ? 1 : 0,
        address: this.address,
        province: this.position.province,
        city: this.position.city,
        area: this.position.district,
        lat: this.position.point.lat,
        lon: this.position.point.lng,
        isDefault: 1
      }
      this.ajaxSaveAddress(data)
    },
    ajaxSaveAddress (data) {
      minutils.IndicatorOpen('上传中...')
      api.saveAddress(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          sessionStorage.removeItem('searchPosition')
          if (this.sign === 'company') {
            sessionStorage.setItem('companyPosition', JSON.stringify(data))
            this.$router.push('/companyinfo')
          } else {
            sessionStorage.setItem('personPosition', JSON.stringify(data))
            this.$router.push('/personinfo')
          }
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.card-add-address {
  padding-top: .88rem;
  .address-page__list {
    background: @whiteColor;
    margin-bottom: 1rem;
  }

}

</style>



